.new {
    &__list {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;   
    }
    &__item {
        width: calc(50% - 8px);
        display: flex;
        border: 1px solid $border-color;
        border-radius: 4px;
        overflow: hidden;
        margin-bottom: 16px;
        @include sp {
            width: 100%;
        }
    }
    &__img {
        display: block;
        width: 250px;
        min-width: 250px;
        height: 150px;
        @include sp {
            width: 115px;
            min-width: 115px;
            height: 130px;
        }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
    &__content {
        padding: 16px;
        @include sp {
            padding: 8px;
        }
        h4 {
            margin-bottom: 8px;
            a {
                text-decoration: none;
                @include font-config(14px, 500);
                @include text-spacing();
                color: #101010;
                overflow: hidden;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                display: -webkit-box;
            }
        }
        p {
            font-size: 14px;
            font-weight: 400;
            font-style: normal;
            line-height: 145%;
            letter-spacing: 0;
            color: grey;
            overflow: hidden;
            -webkit-line-clamp: 5;
            -webkit-box-orient: vertical;
            display: -webkit-box;
            @include sp {
                -webkit-line-clamp: 3;
            }
        }
    }
} 