.c-carousel{
    width: 100%;
    text-align: center;
    position: relative;
    .slick-dots {
        @include flex-center;
        margin: 0;
        padding: 1rem 0;
        position: absolute;
        bottom: 1.8rem;
        left: 0;
        right: 0;
        list-style-type: none;
        @include sp{
            bottom: 0;
        }

            li {
                margin: 0 0.25rem;
            }
            button {
                display: block;
                width: 1rem;
                height: 1rem;
                @include sp{
                    width: 1rem;
                    height: 1rem;
                }
                padding: 0;
                border: none;
                border-radius: 100%;
                background-color: white;
                text-indent: -9999px;
            }
            li.slick-active button {
                background-color: #EB6A6A;
            }
        
    }
    &_item{
        position: relative;
    }
    .m-btn{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: #ffffff;
        color: #232323;
        padding: 8px 20px;
        border-radius: 5px;
        text-decoration: none;
        font-size: 1.7rem;
        font-weight: 600;
        width: 150px;
        &:hover{
            background: #232323;
            color: #ffffff;
        }
    }
}